header {
  height: 4rem;
  background-color: var(--erie-black);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

html body {
  background-color: var(--erie-black);
}

header h1 {
  margin-left: 1.5rem;
  padding-top: 0.5rem;
  color: var(--ghost-white);
}

#login {
  margin-right: 1rem;
  border: 2px solid black;
  padding: 0.2rem 0.5rem;
  background-color: var(--platinum);
  border-radius: 0.4rem;
  font-weight: 500;
}

#login:active {
  background-color: var(--ghost-white);
  margin-top: 1.5rem + 0.1rem;
  margin-left: 20% + 0.1rem;
  margin-bottom: -0.1rem;
  margin-right: 0.9rem;
}

#login:hover {
  background-color: var(--alabaster);
  cursor: pointer;
}

#intro {
  display: flex;
  text-align: center;
  width: 90%;
  margin: 0 5%;
  margin-top: 2rem;
  background-color: var(--platinum);
  border-radius: 2rem;
  padding: 0.8rem 0.5rem;
  justify-content: space-around;
  font-size: 1.3rem;
  border: 4px solid var(--alabaster);
}

#register {
  width: 60%;
  background-color: var(--anti-flash-white);
  border-radius: 2rem;
  padding: 0.6rem 0.3rem;
  margin: 0 20%;
  margin-top: 1.5rem;
  border: 4px solid var(--ghost-white);
  font-size: 1.4rem;
}

#register:hover {
  background-color: var(--platinum);
}

#register:active {
  background-color: var(--alabaster);
  margin-top: calc(1.5rem + 0.1rem);
  margin-left: calc(20% + 0.1rem);
  margin-bottom: -0.1rem;
  margin-right: -0.1rem;
}

@media only screen and (min-width: 480px) {
  #intro {
    max-width: 380px;
    margin: 1.5rem auto;
  }
}

@media only screen and (min-width: 750px) {
  #intro {
    margin: 2.5rem auto;
  }

  #register {
    width: 14%;
    margin: 0 43%;
    margin-top: -0.75rem;
  }

  #register:active {
    background-color: var(--alabaster);
    margin-top: -0.65rem;
    margin-left: calc(43% + 0.1rem);
    margin-bottom: -0.1rem;
    margin-right: calc(43% - 0.1rem);
  }

  header {
    margin-top: 1rem;
  }

  header h1 {
    margin-left: 6rem;
  }

  header #login {
    margin-right: 5rem;
  }

  #login:active {
    margin-top: 1.5rem + 0.1rem;
    margin-left: 20% + 0.1rem;
    margin-bottom: -0.1rem;
    margin-right: 4.9rem;
  }
}